import {HomeUrl, MessageUrl, VehicleUrl, CollectUrl, MallUrl} from '../config/urls'
import axios from 'axios'

// message板块
/**
 * 获取message列表
 * */
export const getMessageList = function (page) {
  return new Promise((resolve, reject) => {
    axios.post(MessageUrl.getMessageList, {page: page})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}
/**
 * 删除某条消息
 * @param {int} messageID 消息ID
 * */
export const removeOneMessage = function (messageID) {
  return new Promise((resolve, reject) => {
    axios.post(MessageUrl.removeMessage, {message_id: messageID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 车辆板块
export const getVehicleList = function () {
  return new Promise((resolve, reject) => {
    axios.get(VehicleUrl.getVehicleList)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 添加我的车辆
 * @param data {Object}
 * */
export const addVehicleFn = (data) => {
  return new Promise((resolve, reject) => {
    axios.post(VehicleUrl.addVehicle, data)
      .then(data => {
        resolve(data.data)
      })
      .catch(err => reject(err))
  })
}

/**
 * 删除车辆
 * */
export const removeVehicle = (customerID) => {
  return new Promise((resolve, reject) => {
    axios.post(VehicleUrl.removeVehicle, {customer_id: customerID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 订单板块

/**
 * 获取订单列表，如不传值返回所有订单
 * @param {bool} unpaid 未支付列表
 * */
export const getOrderList = function (unpaid, page) {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.orderList, {unpaid: unpaid, page: page})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 删除订单
 * */
export const removeOrder = function (orderID) {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.deleteOrder, {order_no: orderID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 收藏板块

/**
 * 获取收藏列表 1为商品收藏2为帖子收藏
 * @param klass
 * */
export const getCollectList = klass => {
  return new Promise((resolve, reject) => {
    axios.post(CollectUrl.getCollectList, {
      class: klass
    })
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 删除收藏
 * @param collect_id
 * */
export const deleteCollect = collectID => {
  return new Promise((resolve, reject) => {
    axios.post(CollectUrl.deleteCollectItem, {collect_id: collectID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 收货地址板块
/**
 * 获取收货地址列表
 * */
export const getAddressList = function () {
  return new Promise((resolve, reject) => {
    axios.get(HomeUrl.addressList)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 增加收货地址
 * */
export const createAddress = (addressName, addressTel, addressCode, address, addressProvince, addressCity) => {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.addAddress, {address_name: addressName, address_tel: addressTel, address_code: addressCode, address: address, address_province: addressProvince, address_city: addressCity})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}
/**
 * 删除某个收货地址
 * @param {int} addressID 收货地址ID
 * @return {Promise}
 * */
export const removeAddress = (addressID) => {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.removeAddress, {address_id: addressID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 设置默认地址
 * */
export const setDefaultAddress = (addressID) => {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.setDefaultAddress, {address_id: addressID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 设置用户信息
export const modifyUserInfo = (k, v) => {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.userModify, {key: k, value: v})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 获取个人中心卡券列表
 */
export const getUserCard = function (page) {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.card, {page: page})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}
/**
 * 上传售后图片
 * @param {file} file 要上传的文件
 */
export const uploadImg = function (file) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.uploadRefund, file, {
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}
/**
 * 提交售后信息
 * @param {string} orderNo  订单id
 * @param {string} imgUrl   上传的图片
 * @param {string} reason   退款原因
 * @param {number} pid
 */
export const submitPayRefundApply = function (orderNo, imgUrl, reason, pid) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.refundApply, {order_no: orderNo, imgurl: imgUrl, reason: reason, pid: pid})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 设置意向商家
export const setMaintain = function (unitId, id) {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.selectMaintain, {unit_id: unitId, id: id})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 赠送卡券
export const giveCoupon = function (code, tel) {
  return new Promise((resolve, reject) => {
    axios.post(HomeUrl.giveCoupon, {coupon: code, tel})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}



