<template>
  <div class="user-wrap">
    <app-header
      title="个人设置"
      showBack="true"
    ></app-header>
    <div class="user-main">
      <div class="info">
        <div>
          <div class="title">我的账户</div>
          <a href="javascript:void(0)" class="mint-cell mint-field">
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><span class="mint-cell-title">积分：</span></div>
              <div class="mint-cell-value">{{userInfo.points}}</div>
              <router-link to="/integral" style="color:seagreen;">积分规则</router-link>
            </div>
          </a>
          <a href="javascript:void(0)" class="mint-cell mint-field">
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><span class="mint-cell-title">等级：</span></div>
              <div class="mint-cell-value">{{userInfo.empirical_value}}</div>
            </div>
          </a>
          <a href="javascript:void(0)" class="mint-cell mint-field">
            <div class="mint-cell-wrapper">
              <div class="mint-cell-title"><span class="mint-cell-title">推荐码：</span></div>
              <div class="mint-cell-value">{{userInfo.invite_code}}</div>
            </div>
          </a>
        </div>
      </div>
      <div>
        <div class="title">修改信息</div>
        <mt-field label="我的昵称" placeholder="请输入您的昵称" v-model="likeName" @change="cname"></mt-field>
        <mt-field label="我的生日" placeholder="请输入您的生日" v-model="birth" type="date" @change="cbirth"></mt-field>
        <mt-field label="身份证号" placeholder="请输入您的身份证号" v-model="uid" @change="cidentity"></mt-field>
        <mt-button type="danger" plain size="large" style="margin-top: 1rem;" @click="out">退出登陆</mt-button>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import {loginOut} from '../../../api/login'
import {modifyUserInfo} from '../../../api/home'
import {getLocalStorage} from '../../../lib/util'
export default {
  name: 'Home-User',
  data () {
    return {
      likeName: '',
      birth: '',
      addr: '',
      uid: '',
      userInfo: {}
    }
  },
  created () {
    this.$set(this, 'userInfo', JSON.parse(getLocalStorage('userInfo')))
    this.$set(this, 'likeName', this.userInfo.nickname)
    this.$set(this, 'birth', this.userInfo.birth)
    this.$set(this, 'uid', this.userInfo.identity_id)
  },
  watch: {
  },
  methods: {
    out () {
      loginOut()
        .then(data => {
          if (data) {
            this.$toast(data.errormsg)
            this.$router.replace('/mall')
          } else {
            this.$toast(data.errormsg)
          }
        })
    },
    cname (val) {
      modifyUserInfo('nickname', val).then(() => this.$toast('成功'))
    },
    cbirth (val) {
      modifyUserInfo('birth', val).then(() => this.$toast('成功'))
    },
    cidentity (val) {
      modifyUserInfo('identity_id', val).then(() => this.$toast('成功'))
    }
  },
  components: {
    AppHeader
  }
}
</script>

<style lang="less" scoped>
  .user-wrap{
    height: 100%;
    background-color: #e5e5e5;
    .user-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #e5e5e5;
      &:after{
        content: " ";
        height: 5rem;
        display: block;
      }
    }
  }
  .title{
    padding: .6rem;
    color: #999;
  }
</style>



